import infoText from '../../components/m-info-text/m-info-text'
import mButton from '../../components/m-button.vue'
import uniIcon from '../../components/uni-icon/uni-icon.vue'
export default {
	data() {
		return {
			titles:'下单购油',
			addrTitles:'选择地址',
			coutomerTitles:'选择客户经理',
			companyTitles:'选择公司',
			company: "",
			productOil: '选择油品',
			modeOil: "选择提油方式",
			modePay: '请选择付款方式',
			address: "请选择地址",
			count: '',
			Remarks: '',
			show: false,
			mode: false,
			pay: false,
			addrShow: true,
			infos: {
				placeholder: '请输入数量',
				buyoilText: '购买数量(吨)',
				number: 'number',
				muchOil: "",
			},
			btn: {
				primary: "primary",
				btnvalue: "提交意向单",
			},
			info: [], //地址
			range: 0,
			chooseAddress: false,
			getTpe: '',
			myManager: '', //客户经理
			newDatas: [],
			showCompany: false,
			companyId: '',
			showCoutomer: false,
			page: 1,
			pageSize: 10,
			man: [],
			more: true,
			value: '',
			myManagerId: '',

		}
	},
	onLoad() {
		this.getCompanyInfo();
		this.getAddressInfo();
	},

	methods: {
		//获取默认公司
		getCompanyInfo() {
			const that = this;
			this.test.post('user/order_company')
				.then(res => {
					console.log(res)
					// 
					if (res.statusCode == 200 && res.data.errorCode == 0) {
						// res.data.value.forEach(el => {
						// 	const info = el
						that.company = res.data.value.name;
						that.companyId = res.data.value.id 
						// })
					} else {

					}


				}).catch(err => {
					console.log(err)
				});
			uni.getStorage({
				key: 'userInfo',
				success: function(res) {
					// console.log(res)
					that.myManager = res.data.user.manager_name; //客户经理信息
					that.myManagerId = res.data.user.manager_id; //客户经理信息
				}
			})
		},
		getNewCompany() {
			const that = this;
			this.page = 1;
			this.showCompany = true;
			this.test.post('order/listOrgs')
				.then(res => {
					console.log(res)
					that.newDatas = res.data.value;

				})
		},


		//选择公司
		chooseCompany(e) {
			const that = this;
			console.log(this.newDatas)
			this.more = true;
			this.company = this.newDatas[e].name;
			this.companyId = this.newDatas[e].id;
			this.showCompany = !this.showCompany;
			this.test.post('base/listManagers', { //选择公司后默认第一个客户经理
					realname: this.value,
					org_id: this.companyId,
					size: this.page,
					pageSize: this.pageSize,
				})
				.then(res => {
					// that.newDatas = res.data.value
					if (res.statusCode == 200 && res.data.errorCode == 0) {
						// console.log()
						that.man = [];
						res.data.value.forEach(el => {
							that.man.push(el)
						})
						this.myManager = this.man[0].realname;
						if (res.data.value.length < 10 && res.data.value.length > 0) {
							this.more = false;
						} else if (res.data.value.length == 0) {
							this.more = false;
							uni.showToast({
								title: '没有更多了',
								icon: "none"
							})
						}
					}
				})
			console.log(this.man)


		},

		getNewCustemerInfo() {
				this.man = [];
				this.page = 1;
				this.more = true;
				this.getNewCustemer();
				this.showCoutomer = true;
		},
		getNewCustemer() {
			console.log(this.value)
			this.test.post('base/listManagers', {
					realname: this.value,
					org_id: this.companyId,
					size: this.page,
					pageSize: this.pageSize,
				})
				.then(res => {
					// that.newDatas = res.data.value
					console.log(res)
					if (res.statusCode == 200 && res.data.errorCode == 0) {
						// console.log()
						res.data.value.forEach(el => {
							this.man.push(el)
						})
						this.myManager = this.man[0].realname;
						if (res.data.value.length < 10 && res.data.value.length > 0) {
							this.more = false;
						} else if (res.data.value.length == 0) {
							this.more = false;
							uni.showToast({
								title: '没有更多了',
								icon: "none"
							})
						}
					}
				})


		},

		// 搜索客户经理
		searchCustomer(e) {
			console.log(e)
			this.page = 1;
			if (e.target.value !== '' && e.target.value !== null) {
				this.getNewCustemerInfo();
			} else {
				this.value = '';
				this.getNewCustemerInfo();
			}
		},
		chooseCustomers(e, id) {
			this.myManagerId = id;
			this.myManager = this.man[e].realname;
			this.showCoutomer = !this.showCoutomer
		},
		// 点击加载更多
		Smore() {
			this.page += 1;
			this.getNewCustemer()
		},
		// 付款方式
		payShow() {
			this.pay = !this.pay
		},
		chooseOilShowPay() {
			this.pay = !this.pay
		},
		//选择油品
		chooseOilShow() {
			this.show = !this.show;
		},
		// 取消
		chooseOilLeave() {
			this.mode = !this.mode;
		},
		chooseOne(val) {
			console.log(val)
			this.show = !this.show;
			this.productOil = val.target.id;
			//  this.$refs.chooseOne.className="modelmainActive"
		},
		pays(val) {
			this.pay = !this.pay;
			this.modePay = val.target.id
		},
		modeShow() {
			this.mode = !this.mode;
		},
		chooseTwo(val) {
			console.log(val);
			this.mode = !this.mode;
			this.getTpe = val.target.id
			if (val.target.id == '配送') {

				this.addrShow = true
			} else {

				this.addrShow = false
			}
			this.modeOil = val.target.id;

		},

		chooseAddr() {
			this.getAddressInfo();
			this.chooseAddress = !this.chooseAddress;
			uni.showToast({
				title: '选择你要收货的地址,然后点确认',
				icon: 'none'
			})
		},
		// 获取地址信息
		getAddressInfo() {
			const that = this;
			this.test.post('user/getAddrList').then(res => {
				if (res.statusCode == 200 && res.data.errorCode == 0) {
					this.info = res.data.value;
					//  console.log(this.info)
					this.info.forEach(el => {
						// console.log(el)
						if (el.is_default == 1) {
							that.address = el.address
						}
					})
				}
			}).catch(err => {
				console.log(err)
			})
		},
		//设置输入框只能为正整数
		setNumber(val) {
			console.log(val)
			if (val == 0) {
				this.count = ''
				uni.showToast({
					title: '购买数量不能为0',
					icon: 'none'
				})
			} else if (val !== 0) {
				this.count = this.count.replace(/^(0+)|[^\d]+/g, '')
			}

		},



		// 选择地址
		isAddress(inx) {
			console.log(inx, this.info)
			const that = this;
			uni.showModal({
				content: '确定选择该地址为收货地址',
				success: function(res) {
					if (res.confirm) {
						that.address = that.info[inx].address;
						that.chooseAddress = !that.chooseAddress
						console.log(that.address)
					} else if (res.cancel) {
						return;
					}

				}
			})

		},
		sure(e) {
			uni.showModal({
				"title": "提示",
				"content": '确认选择该地址为默认地址？',
				success: res => {
					if (res.confirm) {
						for (let i = 0; i < this.info.length; i++) {
							if (this.info[i].is_default == e.target.value) {
								this.range = i;
								console.log(this.info[i].id)
								this.test.post("user/setDefaultAddr", {
									addr_id: this.info[i].id
								}).then(res => {
									console.log(res)
									if (res.statusCode == 200 && res.data.errorCode == 0) {
										uni.showToast({
											title: '设置成功'
										})
									}
								})
								break;
							}
						}
						// 点击取消
					} else if (res.cancel) {
						this.info = '',
							this.getAddressInfo()
					}
				}
			})
		},

		toBuy() {
			const that = this;
			if (this.productOil !== null && this.productOil !== '' && this.productOil !== '选择油品') {
				if (this.getTpe !== null && this.getTpe !== '') {
					if (this.modePay !== null && this.modePay !== '' && this.modePay !== '请选择付款方式') {
						if (this.count !== null && this.count !== '') {

							uni.showModal({
								title: '提示',
								content: '提交后无法修改，是否提交',
								success: function(res) {
									if (res.confirm) {
										that.test.post('order/make_order', { //http://192.168.0.156:8080/api/bizcust/
											org_id: that.companyId,
											manager_id: that.myManagerId,
											manager_name: that.myManager,
											oil_type: that.productOil,
											get_type: that.getTpe,
											pay_type: that.modePay,
											count: that.count,
											ship_addr: that.address,
											remark: that.Remarks,
										}).then(res => {
											console.log(res)
											if (res.statusCode == 200 && res.data.errorCode == 0) {
												uni.redirectTo({
													url: '../orderList/orderList'
												})
											}  else if(res.data.errorCode == 10001 || res.data.errorCode == 10002 || res.data.errorCode == 10003){
												uni.showModal({
													title: '提示',
													content: '用户信息已失效，请重新登录',
													success: function(res) {
														if (res.confirm) {
															uni.reLaunch({
																url: '../login/login'
															})
														} else {
															uni.reLaunch({
																url: '../login/login'
															})
														}
													}
												})
											}else{
												uni.showToast({
													title: res.data.message,
													icon: "none"
												})
											}
										}).catch(err => {
											console.log(err)
										})
									} else {
										return;
									}
								}
							})



							//购油数量	
						} else {
							uni.showToast({
								title: '请输入购油数量',
								icon: 'none'
							})
						}
						//付款方式
					} else {
						uni.showToast({
							title: '请选择付款方式',
							icon: 'none'
						})
					}
					//提油	方式
				} else {
					uni.showToast({
						title: '请选择提油方式',
						icon: 'none'
					})
				}
				//油品	
			} else {
				uni.showToast({
					title: '请选择油品',
					icon: 'none'
				})
			}

		},
	},

	components: {
		mButton,
		infoText,
		uniIcon
	},
	mounted() {
		// setTimeout(() => { }, 0),
		setTimeout(function() {
			this.company = "小花"
		}, 0);
	}
}
